<template>
	<div>
		<template v-if="editCell && editorInline">
			<div v-if="field.type    === 'checkbox'">
	            <div class="list_checkbox checkbox">
	                <label>
	                    <input type="checkbox"
                               :ref="field.type"
                               :id="items.item.id"
                               v-focus
                               v-model="items.item[field.key]"
	                    >
	                    <span class="checkbox-custom"><i class="fa fa-check"></i></span>
	                </label>
	            </div>
	        </div>
			<template v-else-if="field.type === 'select'">
				<v-select :ref="field.type"
				          item-text="name"
				          item-value="last"
						  @input="chooseOption(field,items.item)"
						  :value="items.item[field.key]"
				          :name="field.key"
						  :label="field.options_data.options_field_title"
						  :class="{'valid':fieldsVV[field.key]&&fieldsVV[field.key].valid&&fieldsVV[field.key].touched && field.validation, 'invalid':fieldsVV[field.key]&&fieldsVV[field.key].invalid&&fieldsVV[field.key].touched && field.validation}"
				          :options="getOptionSelect(items.item,field)"
				          :reduce="obj => obj[field.options_data.options_field_id]"
				          v-validate="field.validation"
				          v-model="items.item[field.key]">
					<div slot="no-options">Ничего не найденно!</div>
				</v-select>
			</template>
			<template v-else-if="field.type === 'date'">
				<datepicker
							:ref="field.type"
							v-focus
							v-validate="field.validation"
							:name="field.key"
				            :format="customFormatter"
				            v-model="items.item[field.key]"
				></datepicker>
			</template>
			<template v-else>
				<input type="text"
				       v-focus
				       :ref="field.type"
				       class="inlineEditor__text"
				       v-model="items.item[field.key]"
				       v-validate="field.validation"
				       :name="field.key"
				>
			</template>
			<div class="undo btn btn-green-small" @click="undoChanges" v-if="errors.first(field.key)">
				<i class="fa fa-undo" aria-hidden="true"></i>
			</div>
			<div class="error" :content="errors.first(field.key)"
			     v-if="errors.first(field.key)"
			                    v-tippy="{
                                    placement : 'left',
                                    animation : 'fade',
                                    distance  : '-0',
                                    offset    : '0,5',
                                    delay     : [250,250],
                                    arrow     : false,
                                    theme     : 'error'
                               }">
				<i class="fa fa-info-circle" aria-hidden="true"></i>
			</div>
		</template>
        <div class="parag-wrapper" v-if="!editCell" >
	        <template v-if="field.type         === 'html'"
	             :style="{ paddingLeft: items.item.padding + 'px'}"
	        >
		        <span v-html="items.value"></span>
		    </template>
	        <template v-else-if="field.type    === 'label'"
	             class="parag-wrapper"
	             :style="{'background-color':(items.item[field.key] && items.item[field.key].color) || false}"
	        >
	            <p v-if="field.typeVal === 'date' && field.format">
	                {{deepValue(items.value) | moment(field.format)}}
	            </p>
	            <p v-else-if="field.typeVal === 'number' && field.format">
	                <template v-if="items.item[field.key].value.length === 0">
	                    {{deepValue(items.value)}}
	                </template>
	                <template v-else>
	                    {{deepValue(items.value) | numeralFormat(field.format).replace(/,/g,' ')}}
	                </template>
	            </p>
	            <p v-else>
	                {{deepValue(items.value)}}
	            </p>
	        </template>
	        <template v-else-if="field.type    === 'checkbox'">
	            <div class="list_checkbox checkbox checkbox-all">
	                <label>
	                    <input type="checkbox" :id="items.item.id"
	                           :checked="items.item[field.key]" disabled
	                    >
	                    <span class="checkbox-custom"><i class="fa fa-check"></i></span>
	                </label>
	            </div>
	        </template>
	        <template v-else-if="field.type    === 'select'"
	             class="parag-wrapper"
	        >
		        {{getSelectValue(items.item,field)}}
	        </template>
	        <template v-else-if="field.typeVal === 'date' && field.format">
	            <p>
	                {{deepValue(items.value) | moment(field.format)}}
	            </p>
	        </template>
	        <template v-else-if="field.typeVal === 'number' && field.format">
	            <p>
	                {{deepValue(items.value) | numeralFormat(field.format).replace(/,/g,' ')}}
	            </p>
	        </template>
	        <template v-else
	        >
	            {{$getProp(items.item, field.key)}}
	        </template>
        </div>
	</div>
</template>

<script>
	import Datepicker from 'vuejs-datepicker';
	import vSelect from 'vue-select'
    import {editorTable} from '../../mixins/editorTable'
    import {inputHandler} from '../../mixins/inputHandler'
	export default {
	    name: "tableCell",
		mixins: [editorTable,inputHandler],
		props: {
	        add_models: Object,
	        field: {
	            type: Object,
		        default: {}
	        },
	        fields: {
	            type: Array,
		        default: []
	        },
	        block: Object,
            editorData: {
	            type: Object,
		        default: {}
	        },
	        items: {
	            type: Object,
		        default: {}
	        },

            editorCellData: {
	            type: Object,
		        default: {
	                field: {},
	                item: {},
	            }
	        }
		},
		
		methods:{
            customFormatter(date) {
                return this.$moment(date).format(this.field.format);
            },
	        deepValue(value) {
	            return value.hasOwnProperty('value') ? value.value : value;
	        },
            getSelectValue(item,field){
	            let answer = '';
				this.add_models[field.options_data.options_data_model].forEach(e=>{
				    if(e[field.options_data.options_field_id] === item[field.key]){
                        answer = e[field.options_data.options_field_title]
				    }
				})
	            return answer;
            },
		},
		watch:{
	        '$refs'(){
                this.$nextTick(() => console.log(this.$refs))
	            console.log(this.$refs)
			}
		},
		computed:{
	        editCell(){
	            return this.field.edit &&
		            this.field.key === this.editorCellData.field.key &&
		            this.items.item === this.editorCellData.item;
	        },
            editorInline(){
	            return this.editorData.editor && this.editorData.editorMode === 'inline';
			},
			
		},
		components:{
            vSelect,
            Datepicker
		}
	}
</script>

<style lang="scss">
	@import "../../assets/sass/variables";
	// inlineEditor
	.inlineEditor{
		td.editable{
			position: relative;
			cursor: pointer;
			&:after{
				content: '\f040';
				font-size: 12px;
				font-family: "FontAwesome";
				position: absolute;
				top: 1px;
				right: 1px;
				opacity: 0;
				transition: opacity .25s;
			}
			&:hover,&.editCell{
				&:after{
					opacity: 1;
				}
			}
			.select__input-hidden{
				position: absolute;
				left: -100%;
			}
		}
		.error{
			position: absolute;
			top: 1px;
			left: 1px;
			cursor: help;
			font-size: 13px;
			color: $invalid;
		}
		tr{
			&:hover{
				transform: none !important;
				box-shadow: none !important;
			}
		}
		.vdp-datepicker__calendar{
			width: 20rem !important;
		}
		&__text,.vdp-datepicker input{
			border: 0;
			width: 100%;
			border-bottom: 1px solid $accent;
		}
		.vs__dropdown-toggle{
			padding: 0;
			border: 0;
		}
		.vs__search {
			margin-top: 0 !important;
			height: 2rem !important;
			border: none !important;
		}
		.v-select{
			margin-top: 0;
			border-bottom: 1px solid $accent;
		}
		.vs__dropdown-menu {
			z-index: 9999;
		}
		.vs--single .vs__selected{
			padding: 0;
			margin: 0;
			border: 0;
		}
		.vs__selected-options{
			padding: 0;
		}

	}
</style>
