export const dragAndDrop = {
    data: function () {
        return {
            dragDropEnable: false,
            dragDrop: {
                item: {},
                index: 0,
                tr: null,
                newIndex: 0,
            },
            dragDropInitialization: false,
        }
    },
    methods:{
        dragAndDrop(selectorLines,selectorTable){
            this.dragDropEnable = !this.dragDropEnable;
            if(this.block.block_parameters.drag_and_drop !== true) return false;
            if(this.dragDropInitialization) return false
            else  this.dragDropInitialization = true;
            let self = this;
            let trs = document.querySelectorAll('tbody tr');
            // Разделители
            let trsSeparators = [...trs].map((tr)=>tr.offsetTop);
            trsSeparators.unshift(0);

            let table = document.querySelector('#list_table');
            // Состояние нажатия
            let moveElementTr = false;
            /*
            * Добавление события клика на TR`s
            * */
            [...trs].forEach((e,index)=>e.addEventListener("mousedown",function(event) {
                if(self.dragDropEnable === false) return false;
                let moveElementTr = true;
                let tr   = e;
                self.dragDrop.item = self.tableValues[index];
                self.dragDrop.index = index;
                self.dragDrop.tr = tr;

                let coords = getCoords(e);
                let shiftX = event.pageX - coords.left;
                let shiftY = event.pageY - coords.top;

                /*
                * Создание и настройка теневого элемента
                * */
                function createShadowElem(){
                    let shadowTr = tr.cloneNode(true);
                    let shadowTds = shadowTr.children;
                    [...trs[0].children].forEach((elem,indexElem)=>
                        shadowTr.children[indexElem].style.width = elem.offsetWidth + 'px'
                    );
                    shadowTr.style.position = 'fixed';
                    shadowTr.style.zIndex = 1000;
                    shadowTr.style.width = tr.offsetWidth + 'px';
                    shadowTr.style.transition = '.1s linear';
                    shadowTr.style.opacity = '0.6';

                    return  document.getElementsByTagName('tbody')[0].appendChild(shadowTr);
                }
                let shadowDomTr = createShadowElem();
                shadowDomTr.style.left = event.pageX - shiftX + 'px';
                shadowDomTr.style.top =  (event.pageY - shiftY) - window.scrollY + 'px';

                /*
                * Получение координат элемента
                * */
                function getCoords(elem) {
                    var box = elem.getBoundingClientRect();
                    return {
                        top: box.top + pageYOffset,
                        left: box.left + pageXOffset
                    };
                }

                /*
                * Тормозящий  декоратор
                * */
                function throttled(delay, fn) {
                    let lastCall = 0;
                    return function (...args) {
                        const now = (new Date).getTime();
                        if (now - lastCall < delay) {
                            return;
                        }
                        lastCall = now;
                        return fn(...args);
                    }
                }

                /*
                * Подстветка линии для переноса
                * */
                function backlightLines(index = null){
                    let trsLines = [...trs];
                    trsLines.unshift(document.querySelector('thead tr'));
                    trsLines.forEach(tr=>tr.classList.remove('dragSelect'));
                    if(index === null) return false;
                    let trLine = index === 0 ? trsLines[0] :  trsLines[index];
                    trLine ? trLine.classList.add('dragSelect') : false;
                }

                /*
                * Перенос элемента массива
                * */
                function array_move(arr, old_index, new_index) {
                    console.log(old_index,new_index);
                    if (new_index >= arr.length) {
                        var k = new_index - arr.length + 1;
                        while (k--) {
                            arr.push(undefined);
                        }
                    }
                    arr.splice(new_index, 0, arr.splice(old_index, 1)[0]);
                    return arr;
                };

                /*
                * Минимальное близкое и максимально близкое число
                * */
                function minMaxCoincidence(number){
                    var closestLeft,
                        closestRight,
                        current;
                    for (var i = 0; i < trsSeparators.length; i++) {
                        current = trsSeparators[i];
                        if (current < number && (typeof closestLeft === 'undefined' || closestLeft < current)) {
                            closestLeft = current;
                        } else if (current > number && (typeof closestRight === 'undefined' || closestRight > current)) {
                            closestRight = current;
                        }
                    }
                    return {closestLeft,closestRight}
                }

                /*
                * Обработка движения мыши
                * */
                function moveMouse(ev) {
                    if(moveElementTr === false) return false;
                    let {closestLeft,closestRight} = minMaxCoincidence(getCoords(shadowDomTr).top - getCoords(table).top)
                    let index = trsSeparators.indexOf(closestLeft);
                    index === -1 ? index  = trsSeparators.indexOf(closestRight) : false;
                    backlightLines(index);
                    self.dragDrop.newIndex = index;

                    shadowDomTr.style.left = ev.pageX - shiftX + 'px';
                    shadowDomTr.style.top = (ev.pageY - shiftY) - window.scrollY + 'px';
                }

                const timerHandler = throttled(100,moveMouse);
                document.addEventListener("mousemove", timerHandler);

                document.addEventListener("mouseup", function() {
                    moveElement();
                    document.removeEventListener("mousemove", timerHandler);
                });

                /*
                * Перемещение элемента после отжатия кнопки
                * */
                function moveElement() {
                    if(!moveElementTr) return false;
                    moveElementTr = false;
                    shadowDomTr.remove();
                    backlightLines();
                    array_move(self.listItemsStash,self.dragDrop.index,self.dragDrop.newIndex);
                    self.recountLine();
                }
            }));
        },
    }
}
