                                <p>{{translateList['Showing']}} <b>{{countOf}}-{{countTo}}</b> {{translateList.of}} <b>{{totalRows}}</b>
                                </p>
                                <v-select :components={OpenIndicator}
                                          :options="pageOptions"
                                          v-model="perPage"
                                          class="count"
                                ></v-select>
                                <p>{{translateList['onThePage']}}</p>
                            </div>
