                                                                placement : 'left',
                                                                animation : 'fade',
                                                                distance  : '-0',
                                                                offset    : '0,5',
                                                                delay     : [500,250],
                                                                arrow     : false,
                                                                theme     : 'info'
                                                           }"
                                                        @click="massAction(item.code, item.link)"
                                                        class="dropwond_link"
                                                >
                                                    {{item.title}}
                                                </button>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                                </template>
                                <template v-else>
                                    <a class="btn"
                                       :class="{'btn-grey-border':!checkedItems.length, 'btn-light-grey':checkedItems.length }"
                                       v-for="item in sets.list_top_dropdown_actions"
                                       @click="massAction(item.code, item.link)"
                                    >{{item.title}}</a>
                                </template>
                                <a class="btn btn-green" v-for="action in actions" v-if="action.code=='add_row'"
                                   @click="addItem(action.link)"
                                >{{action.title}}</a>
	                            <!--todo: это костыль, надо проработать вывод отдельных действий -->
                            </div>

                            <template v-if="!sets && form_parameters.hideFilter">
