                                        </button>
                                        <button
		                                        v-else-if="item.code === 'receive_documents'"
		                                        @click="openCard(item.code,'/cardRequest')" :id="item.code"
		                                        :title="item.title"
		                                        :class="item.class"
                                        >
                                            {{item.title}}
                                        </button>
                                        <router-link v-else :to="item.to" :id="item.code" :title="item.title"
                                                     :class="item.class"
                                        > {{item.title}}</router-link>
                                    </template>
                                </template>
                                <template v-for="item in sets.list_top_left_request"
                                          v-if="sets && sets.list_top_left_request"
                                >
                                    <template>
                                        <button
		                                        v-if="item.code === 'add'"
		                                        @click="openCard(item.code,'/new')" :id="item.code"
		                                        :title="item.title"
		                                        :class="item.class"
                                        >
                                            {{item.title}}
                                        </button>
                                        <router-link v-else :to="item.to" :id="item.code" :title="item.title"
                                                     :class="item.class"
                                        > {{item.title}}</router-link>
                                    </template>
                                </template>
                            </template>

                        </div>
                    </div>
                    <template v-if="sets && sets.list_top" v-for="item in sets.list_top">
