                            <p>{{translateList.Payment}}:</p>
                        </div>
                    </div>

                    <div class="list-legend-item">
                        <div class="legend-color"></div>
                        <p>{{translateList.Planned}}</p>
                    </div>
                    <div class="list-legend-item">
                        <div class="legend-color legend-grey"></div>
                        <p>{{translateList.Paid}}</p>
                    </div>
                    <div class="list-legend-item">
                        <div class="legend-color legend-red"></div>
                        <p>{{translateList.Overdue}}</p>
                    </div>
                </div>
                <div class="row">
	                <div class="col" v-if="editor && block_parameters && !block_parameters.edit_mode">
		                <v-select :options="['inline','row']" v-model="editorMode"></v-select>
	                </div>
	                <div class="col d-flex">

                        <!--	                <div class="btn btn-green" v-if="block_parameters.edit_values" @click="handlerEditor" :class="{active:editor}"><i class="fa fa-pencil-square-o" aria-hidden="true"></i></div>-->
                        <div class="btn btn-green  btn-small" @click="addLine" v-if="editor"><i class="fa fa-plus-circle" aria-hidden="true"></i></div>
                        <div class="btn btn-green  btn-small" @click="editLine" v-if="editorRowSelect && editor"><i class="fa fa-pencil" aria-hidden="true"></i></div>
                        <div class="btn btn-green  btn-small" @click="removeLine" v-if="editorRowSelect && editor"><i class="fa fa-trash" aria-hidden="true"></i></div>
                        <div class="btn btn-green  btn-small" @click="dragAndDrop" :class="{active:dragDropEnable}" v-if="block_parameters.drag_and_drop"><i class="fa fa-arrows" aria-hidden="true"></i></div>

                        <!-- Порядок -->
