                                <i class="fa fa-arrow-up" aria-hidden="true"></i>
                            </div>
                            <div class="btn btn-green btn-small" @click="changeOrder(false)">
                                <i class="fa fa-arrow-down" aria-hidden="true"></i>
                            </div>
                        </div>
                    </div>
                </div>
                <!--Таблица-->
                <div class="row">
                    <div class="col">
