                                            'table__thead__overflow':!this.form_parameters.list_header_break_line,
                                            'table_thead_break_line':this.form_parameters.list_header_break_line
                                        }">{{head.label}}</div>
                                    </template>
	                                <!--Слот для фильтров-->
                                    <template :slot="'HEAD_'+field.key"
