                                            <filter-element
		                                            :field="field"
		                                            :exFilterOn="exFilterOn"
		                                            :form_parameters="form_parameters"
		                                            :type="type || ''"
		                                            @sortingChanged="sortingChanged"
		                                            @refreshItems="(items)=>{refreshItems(items)}"
                                            >
                                            </filter-element>


                                            <div class="sorting"
                                                 v-show="!exFilterOn"
                                                 v-if="field.sortable"
                                                 :class="{ascending : sortBy === field.key && sortDesc,descending: sortBy === field.key && !sortDesc}"
                                            ></div>
                                        </div>

                                    </template>
	                                <!--Слот для всех ячеек-->
                                    <template v-for="field in fields" :slot="field.key"
                                              slot-scope="items"
                                    >
	                                    <tableCell
			                                    :editorData="{editor,editorMode,editorCell,editorItem}"
			                                    :items="items"
			                                    :field="field"
			                                    :fields="fields"
			                                    :add_models="add_models"
                                                :block="block"
			                                    :editorCellData.sync="editorCell"
	                                    ></tableCell>
	                                    
                                    </template>
	                                <!--Слот для выбора всех элемента-->
                                    <template slot="HEAD_actions" slot-scope="head">
                                        <div class="list_checkbox checkbox checkbox-all">
                                            <label for="checkbox-all" @click.stop>
                                                <input type="checkbox" id="checkbox-all" @click.stop="toggleSelected"
                                                       v-model="allSelected"
                                                >
                                                <span class="checkbox-custom"><i class="fa fa-check"></i></span>
                                            </label>
                                        </div>
                                    </template>
	                                <!--Слот для выбора одного элемента-->
