                                            <label :for="items.item.id">
                                                <input type="checkbox"
                                                       :id="items.item.id"
                                                       :key="items.index"
                                                       v-model="checkedItems"
                                                       :value="items.item.id"
                                                >
                                                <span class="checkbox-custom"><i class="fa fa-check"></i></span>
                                            </label>
                                        </div>
                                    </template>
	                                <!--Слот для формата отчёта-->
