                                    <p>{{translateList.Showing}} <b>{{countOf}}-{{countTo}}</b> {{translateList.of}} <b>{{totalRows}}</b>
                                    </p>
                                    <v-select :components={OpenIndicator} :options="pageOptions"
                                              v-model="perPage"
                                              class="count d-none d-md-block"
                                    ></v-select>
                                    <p>{{translateList.onThePage}}</p>
                                </div>
                                <!--Пагинация-->
                                <b-pagination
		                                :total-rows="totalRows"
		                                :per-page="perPage"
		                                :hide-goto-end-buttons="true"
		                                v-model="currentPage"
		                                @change="changePage"
		                                class="paginator"
                                />
                            </div>
                            <div v-else-if="sets && sets.list_bottom"
                                 class="table_footer d-flex justify-content-between hidden-md-down"
                            >
                                <template v-for="item in sets.list_bottom">
                                    <!--Элементы пагинации-->
                                    <div class="counter_list" v-if="item.code === 'items_per_page'">
                                        <p>{{translateList.Showing}} <b>{{countOf}}-{{countTo}}</b> {{translateList.of}} <b>{{totalRows}}</b>
                                        </p>
	                                    
                                        <v-select :components={OpenIndicator} :options="pageOptions"
                                                  v-model="perPage"
                                                  class="count d-none d-md-block"
                                        ></v-select>
                                        <p>{{translateList.onThePage}}</p>
                                    </div>
                                    <!--Пагинация-->
                                    <b-pagination
		                                    v-if="item.code === 'pagination'"
		                                    :total-rows="totalRows"
		                                    :per-page="perPage"
		                                    :hide-goto-end-buttons="false"
		                                    limit="5"
		                                    v-model="currentPage"
		                                    @change="changePage"
		                                    class="paginator"
                                    />
