<!--				<div class="log" v-if="dev">-->
<!--					<pre>-->
<!--						{{editorCell}}-->
<!--					</pre>-->
<!--				</div>-->
            </div>
        </transition-group>
    </div>
</template>

<script>
    import $ from 'jquery';
    import tableCell from './exTable/tableCell'
    import 'jquery-touchswipe/jquery.touchSwipe';
    import {BTable} from 'bootstrap-vue'
    import bPagination from 'bootstrap-vue/es/components/pagination/pagination';
    import bFormSelect from 'bootstrap-vue/es/components/form-select/form-select';
    import {AtomSpinner} from 'epic-spinners';
    import Datepicker from 'vuejs-datepicker';
    import vSelect from 'vue-select'
    import {mapGetters} from 'vuex';
    import {saveFile} from '../mixins/saveFile'
    import {EventBus} from '../main.js';
    import numeral from 'numeral'
    import {dragAndDrop} from "../mixins/dragAndDrop";
    import {editorTable} from "../mixins/editorTable";
    numeral.localeData().delimiters.thousands = ' ';

    export default {
        mixins: [saveFile,dragAndDrop,editorTable],
        name: 'List',
        props: {
            block: Object,
	        model: Array,
	        type: String,
	        add_models: Object,
        },
        async beforeDestroy() {
            this.saveLocalState ? await this.saveState() : false;
            this.$toast.destroy();
            window.removeEventListener('resize', this.resizeWindowListener)
        },
        data: function () {
            return {
                addModels: [],
                firstLoad: true,
                saveLocalState: false,              //
                loadLocalState: false,              //
	            fullPath:       this.$bRoute.fullPath,
                sets: false,
                dev: true,
                loading: true,                      // Флаг отображения preloader
                block_model: '',
                response: {},                       // Ответ от сервера
                form_parameters: {
                    form_is_dependent: false,
                },
                dependent: false,
                searchNameVal: '',
                checkedItems: [],
                filterSelect: '',
                listItemsStash: {},
                listItemsAll: [],
                tableValues: [],
                listItemsText: '',
                defaultSelectId: '',
                sortBy: '',                         // Ключ колонки для сортировки
                sortDesc: true,                     // Направление сортировки
                section_title: '',                  // Название страницы
                send: '',
                archive: '',
                action: '',
                search: '',
                sizeFields: [],                     // Набор ширин для колонок списка
                filter: null,
                currentPage: 1,                     // Текущая страница пагинации
                countOfMobile: 1,
                quantityRecord: 1,
                perPage: 10,                        // Кол-во элементов на странице
                allSelected: false,
                pageOptions: [10, 20, 50, 100],     // Варианты вывода элементов на странице
                asyncReports: '',
                fields: [],                         // Колонки списка
                buttons: {},
                actions: {},
                exFilterData: [],
                links: [],
                translateList: [],
                itemsDependent: [],
                exFilterOn: false,                   // Включение фильтра
                list_footer: null,
                foot_clone: false,
                OpenIndicator: {
                    render: createElement => createElement('img', {attrs: {src: '/img/dropdown-i.png'}}),
                },

	            errorsInput: false,
                block_parameters: {},
            }
        },
        computed: {
            items(){
                if(this.model && this.model.length > 0) this.listItemsStash = this.model;
                let items = this.listItemsStash;
                if(this.editor){
                    items = items.filter(e=>e.status !== 3)
                }
                return items.length ? items.slice((this.countOf - 1), this.countTo) : [];
            },

            totalRows() {
                return this.listItemsStash.length;
            },
            showSearch() {
                let show = true;
                let windowWidth = window.innerWidth;
                let itemsLength = this.itemsDependent.length;

                if (windowWidth > 320 && itemsLength < 5) show = false;
                if (windowWidth > 768 && itemsLength < 10) show = false;
                if (windowWidth > 1200 && itemsLength < 15) show = false;

                return show;
            },
            hideSearch(){
                return this.block_parameters && this.block_parameters.hide_search === true;
            },
            rowLine() {
                let sets = (this.sets && this.sets.hasOwnProperty('list_top_dropdown_actions')) &&
                    (this.sets && this.sets.hasOwnProperty('list_top_left_command_bar'));
                return sets || Boolean(this.dependent)
            },
            countTo() {
                let countTo = this.perPage * this.currentPage;
                if (countTo > this.totalRows) {
                    return this.totalRows;
                } else {
                    return this.perPage * this.currentPage;
                }
            },
            countOf() {
                if (this.currentPage !== 1) {
                    return this.perPage * this.currentPage - this.perPage + 1;
                }
                return 1;
            },
            filteredList() {
                // Фильтрация массива `itemsDependent`
                let res = this.itemsDependent.filter((elem) => {
                    if (elem[this.dependent.searchFilterVal] === '' || this.filterSelect === '') return true;
                    else return elem[this.dependent.searchFilterVal].toLowerCase().indexOf(this.filterSelect.toLowerCase()) > -1;
                });
                return this.removeDuplicateObject(res, this.dependent.searchFilterVal);
            },
            routeMatchedPath() {
                return this.$bRoute.matched[this.$bRoute.matched.length - 1].path;
            },
        },
        watch: {
            checkedItems(a, b) {
                this.allSelected = (this.tableValues.length === a.length && a.length !== 0) ? true : false;
            },
            perPage(a, b) {
                if (a !== b) {
                    this.saveLocalState && !this.firstLoad ? this.saveState() : false;
                    this.clearSelected();
                    !this.firstLoad ? this.currentPage = 1 : false;
                    // this.items = this.listItemsStash.slice((this.countOf - 1), this.countTo);
                }
            },
            currentPage(a, b) {
                if (a !== b) {
                    this.saveLocalState && !this.firstLoad ? this.saveState() : false;
                    this.clearSelected();
                    // this.items = this.listItemsStash.slice((this.countOf - 1), this.countTo);
                }
            },
            filter(a, b) {
                if (a !== b) {
                    this.clearSelected();
                }
            },
            async(a, b) {
                if (a !== b) {
                    this.clearSelected();
                }
            },
        },
        methods: {
            /**
             * При переходе на `Card` сохранять локальные данные в `LocalStorage`
             */
            saveLocalSettingList() {
                localStorage.setItem('perPage_' + this.routeMatchedPath, this.perPage);
                localStorage.setItem('currentPage_' + this.routeMatchedPath, this.currentPage);
                localStorage.setItem('windowScroll_' + this.routeMatchedPath, window.pageYOffset);
            },
            /**
             * Нажатие на Head Table
             * @param {string} key Ключ колонки
             * @param {object} obj Объект колонки
             * @param {event} event
             */
            headClick(key, obj, event) {
                if (event.target.classList.contains('grip') || !obj.sortable || this.exFilterOn || this.editor || this.block_parameters.edit_values)
                    return false;
                this.sortBy === key ?
                    this.sortDesc = !this.sortDesc :
                    this.sortDesc = false;
                this.sortBy = key;
                this.sortingChanged({sortBy: this.sortBy, sortDesc: this.sortDesc});
            },
            /**
             * Создание новой записи
             * @param {string} code
             * @param {string} url
             */
            async openCard(code, url) {
                //owner_id:this.defaultSelectId
                if (this.form_parameters.form_is_dependent) {
                    var obj = {controllerId: this.$bRoute.params[this.$bRoute.meta.id_field]};
                    if (this.$bRoute.meta.prev_controller_alias) {
                        obj['dependent'] = {
                            controller_alias: this.$bRoute.meta.prev_controller_alias,
                            id: this.$bRoute.params[this.$bRoute.meta.prev_id_field]
                        }
                    }
                    if (this.dependent && this.dependent.dependent_fields && this.dependent.dependent_fields.type === 'select') {
                        let allow_creation_if_empty = this.dependent.hasOwnProperty('allow_creation_if_empty') ? this.dependent.allow_creation_if_empty : false;
                        if (this.defaultSelectId === '' && !allow_creation_if_empty) {
                            this.$toast.error('Выберите зависимый справочник', '', {position: 'topRight'});
                            return;
                        }

                        obj['owner_id'] = this.defaultSelectId;
                        obj['dependent'] = {
                            controller_alias: this.dependent.dependent_fields.options_data.options_data_model,
                            id: this.defaultSelectId
                        }
                    }
                    this.$store.commit("setParamCard", obj);
                }
                this.$bRouter.push(this.$bRoute.path + url);
            },
            callmodal(items, e) {
                /*console.log( items.item.id,items.item.report_format,items.item.report_status );*/
                var self = this, btn = e.target.parentElement;
                btn.classList.remove('active');
                btn.disabled = true;
                this.$http.post('/report/html/file/modal/ajax', {
                    id: items.item.id,
                }).then(res => {
                    // this.$modal.show('modalHtmlReport');
                    /*this.$store.commit('showModal', {type: 'htmlModal', html: res.data});*/
                    btn.disabled = false;
                    btn.classList.add('active');
                    self.$parent.$refs.dmodal.htmlmodal({modal: {htmlModal: true, html: res.data}});
                    // setTimeout(function() {
                    //     $('#modal_view_report').html(res.data);
                    //     $('#modalHtml').css('top',window.pageYOffset);
                    // }, 100);
                });

                //showModal(items.item.id,items.item.report_format,items.item.report_status)
            },
            /**
             * Массовые действие
             * @param {string} code
             * @param {string} url
             */
            massAction(code, url) {
                this.$http.post(url, {items: this.checkedItems, main_model: this.form_parameters.form_base_data_model})
                    .then(async res => {

                        if (code === 'download') {
                            await this.saveFile(res.data);
                            return;
                        }

                        //обновление элементов списка после действия
                        var data = {
                            id: this.$bRoute.params[this.$bRoute.meta.id_field]
                        };
                        if (this.$bRoute.meta.prev_controller_alias) {
                            data['dependent'] = {
                                controller_alias: this.$bRoute.meta.prev_controller_alias,
                                id: this.$bRoute.params[this.$bRoute.meta.prev_id_field]
                            }
                        }
                        this.componentData(this.$bRoute.meta.route, data);
                        this.$store.dispatch('showToast', {
                            template: res.data.template,
                            title: res.data.title,
                            message: res.data.message,
                            position: res.data.position,
                        })
                    })
                    .catch(error => {
                        this.$store.dispatch('showToast', {
                            template: 'error',
                            title: error.response.status,
                            message: error.response.data.message,
                            position: error.response.data.position,
                        })
                    })
            },
            goToUrl(url, type) {
                this.$store.commit("setFaqid", 26);
                if (type == 'internal') {
                    this.$bRouter.push({path: url});
                } else {
                    this.$bRouter.push({path: this.$bRoute.path + url});
                }
            },
            async sortingChanged(ctx) {
                let self = this;
                let deep = false;
                let field = this.fields.filter((e) => e.key === ctx.sortBy);
                field = field[0];
                if (this.items.length === 0 || !this.items[0].hasOwnProperty(ctx.sortBy) || typeof field === 'undefined') return false;
                this.items[0][Object.keys(this.items[0])[0]].hasOwnProperty('value') ?
                    deep = true :
                    deep = false;

                function sortCompare(aRow, bRow) {
                    const a = deep ? aRow[ctx.sortBy].value : aRow[ctx.sortBy];
                    const b = deep ? bRow[ctx.sortBy].value : bRow[ctx.sortBy];
                    const type = field.hasOwnProperty('typeVal') ? field.typeVal : 'string';

                    if (toString(a).length === 0) return -1;
                    if (type === 'number') {
                        return Number(a.toString().replace(/\s/g, '')) <
                        Number(b.toString().replace(/\s/g, '')) ? -1 :
                            Number(a.toString().replace(/\s/g, '')) >
                            Number(b.toString().replace(/\s/g, '')) ? 1 : 0
                    } else if (type === 'date') {
                        return self.$moment(a).isAfter(self.$moment(b)) ? 1 : -1
                    } else {
                        return toString(a).localeCompare(toString(b))
                    }
                }

                if (!ctx.sortDesc) {
                    this.listItemsStash.sort(sortCompare).slice((this.countOf - 1), this.countTo)
                } else {
                    this.listItemsStash.sort(sortCompare).reverse().slice((this.countOf - 1), this.countTo);
                }
                !this.firstLoad ? this.currentPage = 1 : false;

                function toString(value) {
                    if (value === null || typeof value === 'undefined') {
                        return ''
                    } else if (value instanceof Object) {
                        return Object.keys(value)
                            .sort()
                            .map(key => toString(value[self.sortBy]))
                            .join(' ')
                    } else {
                        return String(value)
                    }
                }

                return this.listItemsStash;
            },
            /**
             * Фильтрация таблицы
             * @param {string} searchVal               Текст поиска
             * @param {string} tableColumn             Колонка таблицы
             * @param {string} type                    Тип поиска
             * @param {string} start
             * @param {string} itemId
             * @return {array} this.listItemsStash
             */
            filterList(searchVal, tableColumn, type, start, itemId) {
                let self = this;
                // Проверка типа фильтра
                if (type === 'select' && !start) {
                    if (searchVal !== '') {
                        this.listItemsText = searchVal;
                        this.defaultSelectId = itemId;
                    } else {
                        this.listItemsText = this.translateList.all_select;
                        this.defaultSelectId = '';
                    }
                }
                // Фильтрация
                this.listItemsStash = this.listItemsAll.filter((elem) => {
                    let columnVal = tableColumn && tableColumn.length > 0 ? self.$getProp(elem, tableColumn) : null;
                    if (searchVal === '') return true;
                    if (columnVal === undefined) return false;
                    if (!searchVal) return false;
                    if (type === 'select')
                        return '' + columnVal.toLowerCase() === (searchVal.split(' - ')[1] || searchVal).toLowerCase();
                    if (!tableColumn)
                        return JSON.stringify(elem).toLowerCase().includes(searchVal.toLowerCase());
                    return ('' + columnVal).toLowerCase().includes(searchVal.toLowerCase());
                });
                if (window.innerWidth < 768) {
                    $('.list_mobile').attr('data-item', 1);
                    !this.firstLoad ? this.currentPage = 1 : false;
                    this.countOfMobile = 1;
                }
                this.refreshTable();
            },
