            },
            
	        
            async saveState(){
                await this.$store.dispatch('listState',{
                    perPage: this.perPage,
                    currentPage: this.currentPage,
                    scroll: window.pageYOffset,
                    page: this.fullPath
                });
                console.log('saveState',{
                    perPage: this.perPage,
                    currentPage: this.currentPage,
                    scroll: window.pageYOffset,
                    page: this.fullPath
                });
            },
            async loadState(){
                let state = this.$store.getters.listsList[this.fullPath];
                console.log('loadState',state)
                if(this.firstLoad){
                    this.perPage = state.perPage;
                    await setTimeout(async ()=>{
                        this.currentPage = state.currentPage;
                        window.scrollTo(0, state.scroll);
                    },500)
                }
            },
            checkRouteSaved(){
                let localL = this.$bRoute.meta.save_local_storage_l || false;
                if(!localL){
                    let lists = Object.keys(this.$store.getters.listsList);
                    lists.includes(this.fullPath) ?
	                    this.$store.commit('removeListPage',this.fullPath) :
	                    false;
                }
                let returnPage = this.$store.getters.lastPage.old === this.fullPath;
                this.saveLocalState = localL;
                this.loadLocalState = this.saveLocalState && returnPage;
                console.log('load',this.loadLocalState)
            }

        },
        async created() {
            await this.$http.post('/admin/menu?mode=0&module=main&object=translateList', null)
                .then(res => {
                    this.translateList = res.data;
                    const self = this;
                    if (this.block && this.model) {
                        // Название модели
                        this.block_model = this.block.block_model;
                        // Параметры блока
                        this.block_parameters = this.block.block_parameters;
                        if(this.block_parameters.hide_pagination) this.perPage = 200;
                        // Зависимость формы к select
                        this.form_parameters.form_is_dependent = this.block.form_is_dependent;
                        // дописываем флаг на блокировку клика на списке
                        if (this.block.block_parameters) {
                            // дописываем флаг на блокировку клика на списке
                            this.form_parameters.prevent_list_click = this.block.block_parameters.prevent_list_click;
                            // дописываем флаг на блокировку легенды на списке
                            this.form_parameters.show_list_legend = this.block.block_parameters.show_list_legend;
                            this.form_parameters.hide_filter = this.block.block_parameters.hide_filter;

                            //добавление флага на скрытие пагинации в табличной части
                            this.form_parameters.hide_pagination = this.block.block_parameters.hide_pagination;
                        }
                        // если есть подвал списка - cохраняем его в компоненте
                        if (this.block.list_footer) {
                            this.list_footer = this.block.list_footer;
                            this.foot_clone = true;
                        }
                        // Начальная сортировка
                        this.sortBy = this.block.block_parameters.hasOwnProperty('form_sort_by') ?
                            this.block.block_parameters.form_sort_by :
                            'line_n';
                        // Направление сортировки
                        this.sortDesc = this.block.block_parameters.hasOwnProperty('form_sort_desc') ?
                            this.block.block_parameters.form_sort_desc :
                            false;
                        // Получение всех элементов
                        this.listItemsStash = this.model;
                        // Все элементы
                        this.listItemsAll = this.listItemsStash;
                        // Элементы на странице
                        // this.items = this.listItemsStash.slice(0, this.perPage);
                        // Колонки в списке
                        this.fields = this.block.block_fields;
                        // Колонка для поиска
                        this.searchNameVal = this.block.form_search_field;



                        this.buttons = this.block.block_actions || {};
                        // Проверка на готовность таблицы
                        this.readyTable(this.block_model);
					
                    } else {
                        var data = {
                            id: this.$bRoute.params[this.$bRoute.meta.id_field]
                        };
                        if (this.$bRoute.meta.prev_controller_alias) {
                            data['dependent'] = {
                                controller_alias: this.$bRoute.meta.prev_controller_alias,
                                id: this.$bRoute.params[this.$bRoute.meta.prev_id_field]
                            }
                        }
                        this.componentData(this.$bRoute.meta.route, data);
                    }
                    this.$store.dispatch('showToast', {
                        template: res.data.template,
                        title: res.data.title,
                        message: res.data.message,
                        position: res.data.position,
                    })
                })
                .catch(error => {
                    this.$store.dispatch('showToast', {
                        template: 'error',
                        title: error.response.status,
                        message: error.response.data.message,
                        position: error.response.data.position,
                    })
                })
        },
        mounted() {
            this.checkRouteSaved();
            if (this.$bRoute.path.includes('Dev')) this.dev = true;
            this.actions = {
                view_report: true,
                download_report: true,
                remove_report: true,
            };
            global.list = this;
            window.addEventListener('resize', this.resizeWindowListener);
            this.$store.commit("changeTab", 0);
        },
        components: {
            BTable,
            bPagination,
            bFormSelect,
            AtomSpinner,
            Datepicker,
            vSelect,
            tableCell
        }
    }


</script>

